import React from 'react';
const Header = (props)=>{
  return(
    <>
    <div className="header f">
	   <h1>{props.name}</h1>
     <br/>
      <div>
      	<b>
        <a onClick={()=>{props.handle('about')}}>About</a>
      	<a onClick={()=>{props.handle('cv')}}>CV</a>
      	<a onClick={()=>{props.handle('projects')}}>Projects</a>
      	</b>
      </div>
      <br/>
      <div>
      <b>EN JP CH</b>
      </div>
      <br/>
    </div>
    </>
  )
}

export default Header;