
import React from 'react';
import Header from './Header';
import Body from './Body';
import Footer from './Footer';

class App extends React.Component{

  constructor(props){
    super(props);
    this.state = {
      splash: true,
      count: 0,
      page: 'about'
    }
  }
  setPage = (p)=>{
    this.setState({page:p});
    console.log(this.state.page);
  }
  componentDidMount(){
    document.title = `${this.props.name}`
  };
  renderSplash(){
    /*let splashStyle = {
            height: "100vh", 
            width: "100vw", 
            display: "flex", 
            margin:'0',
            fontSize: '2em',
            justifyContent: "center", 
            alignItems: "center",
            borderRadius: '0'
            {(this.state.splash)?this.renderSplash():<Body time={this.state.count}/>}
        }*/
    setInterval(()=>{
      this.setState({count:this.state.count+1})
      },100);
    setTimeout(()=>{
      this.setState({splash:false})
      },1000);
    return(
      <Body time={this.state.count}/>
    );
  }
  render(){
    return(
      <>
      <Header name={this.props.name} title={this.props.title} handle={this.setPage}/>
      <Body page={this.state.page}/>
      <Footer name={this.props.name}/>
      </>
    );
  }
}

export default App;